:root {
  --backgroundColour: #221F2D;
  --backgroundColourSecondary: #242130;
  --contentBackgroundColour: #353549;
  --textColour: #fff;
  --textColourSecondary: #f2f2f2;
  --headerFont: 'roboto', helvetica, arial;
  --bodyFont: 'roboto', helvetica, arial;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* Columns */
label {
  display: block;
  margin-top: 2em;
  text-align: left;
}

input.valid {
  border: solid 1.5px #82DDA9;
}

input.invalid {
  border: solid 1.5px #ff6644;
}

.btn {
  display: inline-block;
  color: var(--contentBackgroundColour);
  line-height: 1.5;
  font-size: 1rem;
  padding: 0.5em 1em 0.4em;
  background: #fff;
  border: solid 1.5px #fff;
  cursor: pointer;
  font-weight: bold;
}

.submit-btn {
  margin-top: 0.5em;
  border-radius: 5px;
}

h2 {
  font-weight: 300;
}

ul {
  display: -ms-flexbox;
  display: flex;
}

.colour-list {
  display: grid;
  grid-template-columns: repeat(4, minmax(120px, 150px));
  grid-column-gap: 2.5em;
  grid-row-gap: 2.5em;
  position: relative;
}

.colours {
  margin-left: 2.5rem;
}

@media (max-width: 1024px) {
  .colours {
    margin-left: 2rem;
  }
}

@media (max-width: 768px) {
  .colours {
    margin-left: 0;
  }
}

.invisible {
  opacity: 0;
  -webkit-transform: rotateX(-90deg);
  transform: rotateX(-90deg);
}

.hidden {
  display: none;
}

.mouse-enabled *:focus {
  outline: none;
}

.colour-list:hover .colour-add {
  opacity: 1;
}

@-webkit-keyframes fx {
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fx {
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}



